import * as React from 'react';

import { SEO } from 'components/SEO';
import { JobEntry } from 'components/JobEntry';

import './Workpanel.css'

export interface WorkPanelProperties {
  renderCallback: () => void;
}

export class WorkPanel extends React.Component<WorkPanelProperties, {}> {
  public constructor(props) {
    super(props);
    this.props.renderCallback();
  }
  public render() {
    return (
      <>
        <SEO url="professional" />
        <div className="workpanels">
          <div className="entries">
            <JobEntry company="Southpaw Technology" role="Software Developer" time="May 2019 - Present">
              <p>
                
                            </p>
            </JobEntry>
            <div className="job-entry">
              <h3>2019</h3>
              <div className="workpanel-marker workpanel-marker-uoft">
              </div>
              <div className="timeline-entry">
                <div className="timeline-entry-title">
                  <h4><b>University of Toronto</b> Graduated University! <small>May 2019</small></h4>
                </div>
              </div>
            </div>
            <JobEntry company="Southpaw Technology" role="Application Engineer (Intern)" time="May 2018 - May 2019">
              <p>

                            </p>
            </JobEntry>
            <div className="job-entry">
              <h3>2014</h3>
              <div className="workpanel-marker workpanel-marker-uoft">
              </div>
              <div className="timeline-entry">
                <div className="timeline-entry-title">
                  <h4><b>University of Toronto</b> Started University! <small>Fall 2014</small></h4>
                </div>
              </div>
            </div>
          </div>
          <div className="workpanels-content">
          </div>
        </div>
      </>
    );
  }
}



